import React from 'react';
import {Group, CardGrid, Card,
    ModalPage, ModalPageHeader, ModalRoot} from '@vkontakte/vkui';

import Icon28DoneOutline from '@vkontakte/icons/dist/28/done_outline'; //DONE
import Icon28CancelOutline from '@vkontakte/icons/dist/28/cancel_outline'; // NOT STARTED
let Task = ({popoutType, handleClick}) => {

    return (
        <div onClick={() => handleClick(popoutType)} style={{ position: 'relative',}}>
                <div style={{background: 'white', width: '45px', height: '45px', borderRadius: '50%', position: 'absolute',
                    boxShadow: '0 2px 24px 0 rgba(0,0,0,.08), 0 0 2px 0 rgba(0,0,0,.08)',
                    top: -3, left: 5, color: '#2975CC', display: 'flex', justifyContent: 'center', alignItems: 'center', zIndex: 3,
                        }}>
                        <Icon28CancelOutline/>    
                    </div>
                <Card size='1' mode='shadow' style={CardStyles}>
                    <div>
                         Пожелать добра близкому человеку 
                    </div>
                </Card>
        </div>
    );
}

let CardStyles = {
    width: 'calc(100% - 40px)', border: 'none', borderRadius: '5px',
    padding: '10px', fontSize: '15px', justifyContent: 'center', alignItems: 'center',
    paddingLeft: '5px',
    marginLeft: '10px',
    marginBottom: 20,
    display: 'flex',
}


export default Task;


